
@font-face {
  font-family: songke-title;
  src: url(/static/font/songke-title.ttf);
}
.markdown {
  position: relative;
  width: 100%;
  background: rgba(255, 255, 255, 0.4);
  padding-bottom: 7.2rem;
}
.markdown .title {
    font-family: songke-title;
    font-size: 4.32rem;
    color: #507594;
    padding: 9.76rem 3.6rem 1.08rem;
    background: white;
}
.markdown .title span {
      border: 1px solid rgba(80, 117, 148, 0.4);
      padding: 0.9rem 1.2rem;
}
.markdown .center {
    max-width: 750px;
    margin: auto;
}
.markdown .md-display {
    padding: 2.88rem 3.6rem;
    font-size: 1.8rem;
    font-weight: 300;
    font-family: 'Avenir', Helvetica, Arial, sans-serif !important;
}
.markdown code {
    padding: 0;
}
.markdown code div {
      counter-increment: lineId;
      position: relative;
      padding: 0.15rem 0 0.15rem 3.6rem;
      line-height: 1.2;
}
.markdown code div:before {
        content: counter(lineId);
        position: absolute;
        width: 7.2rem;
        left: -5.4rem;
        color: rgba(80, 117, 148, 0.4);
        text-align: right;
}
.markdown code div:hover:before {
        color: #507594;
}
@media only screen and (max-width: 576px) {
.markdown {
      position: relative;
      width: 100%;
      background: rgba(255, 255, 255, 0.4);
      padding-bottom: 4.2rem;
      padding-bottom: 0;
}
.markdown .title {
        font-family: songke-title;
        font-size: 2.52rem;
        color: #507594;
        padding: 7.36rem 2.1rem 0.63rem;
        background: white;
}
.markdown .title span {
          border: 1px solid rgba(80, 117, 148, 0.4);
          padding: 0.525rem 0.7rem;
}
.markdown .center {
        max-width: 750px;
        margin: auto;
}
.markdown .md-display {
        padding: 1.68rem 2.1rem;
        font-size: 1.6rem;
        font-weight: 300;
        font-family: 'Avenir', Helvetica, Arial, sans-serif !important;
}
.markdown code {
        padding: 0;
}
.markdown code div {
          counter-increment: lineId;
          position: relative;
          padding: 0.15rem 0 0.15rem 1.8rem;
          line-height: 1.2;
}
.markdown code div:before {
            content: counter(lineId);
            position: absolute;
            width: 3.6rem;
            left: -2.7rem;
            color: rgba(80, 117, 148, 0.4);
            text-align: right;
}
.markdown code div:hover:before {
            color: #507594;
}
.markdown code div {
        width: 200%;
}
}
.md-display {
  letter-spacing: .8px;
  line-height: 1.5;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: black;
  background: white;
  min-height: 400px;
  word-break: break-all;
}
.md-display h1, .md-display h2, .md-display h3, .md-display h4, .md-display h5, .md-display h6 {
    margin-top: .4em;
    margin-bottom: .2em;
}
.md-display h2 {
    color: #283b4a;
}
.md-display p {
    width: 100%;
    line-height: 1.6;
}
.md-display h1 {
    color: #507594;
    margin-top: 1em;
    line-height: 1.2;
}
.md-display img {
    -ms-flex-item-align: center;
        align-self: center;
    max-width: 80%;
    max-height: 320px;
    border: 1px solid rgba(114, 114, 114, 0.2);
    border-radius: .2rem;
    margin: 0.9rem auto;
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
    display: block;
}
.md-display a {
    opacity: .8;
}
.md-display pre, .md-display blockquote {
    background: rgba(114, 114, 114, 0.05);
    padding: 1.8rem;
    letter-spacing: .4px;
    border: 1px solid rgba(80, 117, 148, 0.4);
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: .2rem;
}
.md-display blockquote {
    margin: 0;
    border-width: 0 0 0 4px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.md-display code, .md-display kbd, .md-display pre, .md-display samp {
    font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
.md-display code {
    padding: 0 .4rem;
    font-size: .9em;
}
.md-display pre {
    -webkit-filter: saturate(1.5) brightness(0.9);
            filter: saturate(1.5) brightness(0.9);
}
.md-display pre code {
      padding: 0;
}
@media only screen and (max-width: 576px) {
.md-display pre {
        overflow-x: scroll;
}
}
.md-display pre, .md-display p, .md-display ul, .md-display ol, .md-display blockquote {
    margin-top: .3em;
    margin-bottom: .3em;
}
#image-preview, #link-preview {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}
#image-preview img, #image-preview iframe, #link-preview img, #link-preview iframe {
    background: #727272;
    -webkit-box-shadow: #727272 0 0 3rem -1rem;
            box-shadow: #727272 0 0 3rem -1rem;
}
#link-preview {
  cursor: w-resize;
}
#link-preview a {
    font-size: 1.8rem;
    line-height: 2;
    letter-spacing: .1rem;
    opacity: 0.6;
}
#link-preview nobr {
    line-height: 2;
    height: 1.8rem;
    word-break: break-all;
    opacity: 0.4;
    overflow: hidden;
    text-overflow: ellipsis;
}
.hljs,
.hljs-subst,
.hljs-function,
.hljs-title,
.hljs-params,
.hljs-formula {
  color: #687b8a !important;
}



